export default {
  data: function () {
    return {
      places: [],
      new_avatar: '',
      new_place: '',
      gender: ''
    }
  },
  created () {
    this.read()
  },
  methods: {
    read () {
      fetch('http://localhost:3000/places')
        .then(res => res.json())
        .then(res => this.places = res)
    },
    create () {
      var ava = 'https://avatars.dicebear.com/v2/' + this.gender + '/:' + '' + this.new_avatar + Math.random() + '' + '.svg'
      fetch('http://localhost:3000/places', {
        method: 'POST',
        body: JSON.stringify({
          title: this.new_place,
          avatar: ava
        }),
        headers: new Headers({'Content-type': 'application/json'})
      }).then(() => {
        this.places.push({
          title: this.new_place,
          avatar: ava
        })
        this.new_place = ''
        this.gender = ''
        this.new_avatar = ''
      })
    },
    update (place) {
      fetch('http://localhost:3000/places/' + place.id, {
        method: 'PUT',
        body: JSON.stringify({
          title: place.title
        }),
        headers: new Headers({'Content-type': 'application/json'})
      })
    },
    remove (place) {
      fetch('http://localhost:3000/places/' + place.id, {
        method: 'DELETE'
      }).then(() => {
        this.places = this.places.filter(item => item !== place)
      })
    }
  }
}

